body {
    font-family: 'Cinzel', serif;
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
    background-color: #1a1a1a !important;
    color: #c0c0c0 !important;
}
#stat-sheet, #battle-view {
    border: 2px solid #4a4a4a;
    border-radius: 5px;
    padding: 20px;
    background-color: #2a2a2a;
    margin-bottom: 20px;
}
#gold-display {
    position: absolute;
    top: 10px;
    right: 20px;
    font-size: 1.5em;
}
.battle-arena {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}
.monster-stats {
    width: 45%;
    padding: 10px;
    border: 1px solid #4a4a4a;
    border-radius: 5px;
    background-color: #333;
}
#battle-log {
    border: 1px solid #4a4a4a;
    border-radius: 5px;
    padding: 10px;
    height: 300px;
    overflow-y: auto;
    background-color: #333;
    font-size: 1.2em;
}
.battle-round {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}
.player-action, .opponent-action {
    width: 45%;
    text-align: center;
}
button {
    margin: 5px;
    background-color: #8B0000;
    border: none;
    border-radius: 3px;
    padding: 10px 20px;
    color: #c0c0c0;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s;
}
button:hover {
    background-color: #A52A2A;
}
#start-battle {
    display: block;
    width: 100%;
    font-size: 1.5em;
    padding: 15px;
    margin-top: 20px;
}
.stats {
    font-size: 1.2em;
}
#message-box {
    position: fixed;
    top: 10%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(0, 0, 0, 0.8);
    border: 2px solid #4a4a4a;
    border-radius: 5px;
    padding: 20px;
    font-size: 1.5em;
    text-align: center;
    z-index: 1000;
    color: #c0c0c0;
}
.hp-bar {
    width: 100%;
    height: 20px;
    background-color: #333;
    border-radius: 3px;
    overflow: hidden;
    margin-top: 5px;
}
.hp-bar-inner {
    height: 100%;
    background-color: #8B0000;
    transition: width 0.5s ease-in-out;
}
@keyframes lunge {
    0% { transform: translateX(0); }
    50% { transform: translateX(10px); }
    100% { transform: translateX(0); }
}
@keyframes block {
    0% { transform: scale(1); }
    50% { transform: scale(1.1); }
    100% { transform: scale(1); }
}
.lunge {
    animation: lunge 0.3s ease-in-out;
}
.block {
    animation: block 0.3s ease-in-out;
}
.styled-input {
    padding: 8px;
    border: 1px solid #4a4a4a;
    border-radius: 4px;
    background-color: #333;
    color: #c0c0c0;
    margin:auto;    
    font-family: 'Cinzel', serif;
    font-size: 1em;
    transition: border-color 0.3s ease-in-out;
}

.styled-input:focus {
    border-color: #8B0000;
    outline: none;
}
.input-grid {
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
    margin: 20px 0;
  }
  
  .stat-item {
    display: flex;
    flex-direction: row;
  }
  
  @media (min-width: 768px) {
    .input-grid {
      grid-template-columns: 1fr 1fr;
    }
  }
  
  .stat-item label {
    margin-bottom: 10px;
  }
  
  .stat-item button {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .stat-item button span {
    margin-left: 5px;
    font-size: 0.9em;
    color: #ffffff;
  }
  